.footer {
  height: 540px;
  position: sticky;
  bottom: 0;
  z-index: -1;
  background-image: url("../images/footer.jpg");
  background-size: cover;
  background-position: center;

  &__logo {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 95px;
  }
}

@media screen and (max-width: $small-screen-breakpoint) {
  .footer {
    height: 240px;
  }
}
