.info {
  display: flex;
  justify-content: space-between;

  &__heading {
    width: 314px;
    font-weight: 300;
    text-transform: uppercase;
    line-height: 60px;
    font-size: 48px;
    margin: 0;
  }

  &__text, {
    max-width: 564px;

    &--first {
      margin-top: 0;
    }

    &--last {
      margin-bottom: 0;
    }
  }
}

@media screen and (max-width: $small-screen-breakpoint) {
  .info {
    flex-direction: column;

    &__heading {
      margin-bottom: 16px;
      margin-top: 0;
      font-size: 36px;
    }
  }
}

@media screen and (max-width: $phone-screen-limit) {
  .info {
    flex-direction: column;
    padding-top: 1px;

    &__heading {
      width: 290px;
    }
  }
}
